import { Component, OnDestroy, OnInit } from '@angular/core'
import { Subscription } from 'rxjs'
import { flatMap, tap } from 'rxjs/operators'
import { Term } from '../common/api-interfaces'
import { TermService } from '../common/services/compliance/term.service'
import { SessionService } from '../common/services/session.service'

@Component({
    selector: 'privacy',
    templateUrl: 'privacy.component.html',
})
export class PrivacyComponent implements OnInit, OnDestroy {
    public term: Term
    public isLoading = false
    private subscriptions = new Subscription()

    constructor(private session: SessionService, private termService: TermService) {}

    public ngOnInit(): void {
        this.subscriptions.add(
            this.session.userStream
                .pipe(
                    tap(() => {
                        this.isLoading = true
                    }),
                    flatMap(user => this.termService.getApplicableTerm(user.id))
                )
                .subscribe(term => {
                    this.term = term
                    this.isLoading = false
                })
        )
    }

    public ngOnDestroy(): void {
        this.subscriptions.unsubscribe()
    }
}
