import { HttpClient } from '@angular/common/http'
import { Injectable } from '@angular/core'
import { Observable } from 'rxjs'
import { map } from 'rxjs/operators'
import { Term, TermsAcceptance } from '../../api-interfaces'

@Injectable({
    providedIn: 'root',
})
export class TermService {
    constructor(private http: HttpClient) {}

    public create(term: Term): Observable<Term> {
        return this.http.post<Term>('/admin/terms', { terms: term.terms, privacy: term.privacy })
    }

    public getApplicableTerm(userId?: string): Observable<Term> {
        return userId
            ? this.getTermsAcceptance(userId).pipe(map(termsAcceptance => termsAcceptance.term))
            : this.http.get<Term>(`/terms`)
    }

    public getTermsAcceptance(userId: string): Observable<TermsAcceptance> {
        return this.http.get<TermsAcceptance>(`/users/${userId}/terms-acceptance`)
    }

    public accept(termsAcceptance: TermsAcceptance, accepted = true): Observable<TermsAcceptance> {
        return this.http.post<TermsAcceptance>(`/users/${termsAcceptance.user.id}/terms-acceptance`, {
            termsAcceptance: {
                id: termsAcceptance.id,
            },
            accepted,
        })
    }
}
